        },
        {
          name: "",
          time: "2019-8-9 12:00:00",
          Chat: "  ",
          role: "",
          who: 2
        },
        {
          name: "",
          time: "2019-8-9 12:00:00",
          Chat: "  ",
          role: "",
          who: 1
        },
        {
          name: "",
          time: "2019-8-9 12:00:00",
          Chat:
            "  ",
          role: "",
          who: 2
        },
        {
          name: "",
          time: "2019-8-9 12:00:00",
          Chat:
            "  ",
          role: "",
          who: 2
        },
        {
          name: "",
          time: "2019-8-9 12:00:00",
          Chat: "  .    ",
          role: "",
          who: 1
        }
      ]
    };
  },
  props:{

  },
  methods: {},
  components: {}
};
</script>

<style scoped>
.el-upload__input {
  display: none !important;
}
img {
  width: 50px;
  height: 50px;
}
</style>



